@mixin for-size($range) {
  $mobile: 320px;
  $tablet: 768px;
  $desktop: 1360px;
  @if $range == phone-only {
    //max-width: 480 - 1 = 479
    // @media (max-width: #{$mobile - 1}) {
    //   @content;
    // }
  } @else if $range == tablet {
    // min-width: 768
    @media (min-width: $tablet) {
      @content;
    }
  } @else if $range == desktop {
    // min-width: 1360
    @media (min-width: $desktop) {
      @content;
    }
  }
}

